<template>
	<div class="civilization">
		<div class="civilization-cot" v-if="yuanguturn.length">
			<h1 v-show="yuanguturn.length">远古时代</h1>
			<ul>
				<li v-for="(item,index) in yuanguturn" @click="clickLia(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="ola">
						<li v-for="ty in yuangu" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="gudianturn.length">古典时期</h1>
			<ul>
				<li v-for="(item,index) in gudianturn" @click="clickLib(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="olb">
						<li v-for="ty in gudian" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="zshijiturn.length">中世纪</h1>
			<ul>
				<li v-for="(item,index) in zshijiturn" @click="clickLic(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="olc">
						<li v-for="ty in zshiji" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="wyfxturn.length">文艺复兴</h1>
			<ul>
				<li v-for="(item,index) in wyfxturn" @click="clickLid(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="old">
						<li v-for="ty in wyfx" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="gongyeturn.length">工业时代</h1>
			<ul>
				<li v-for="(item,index) in gongyeturn" @click="clickLie(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="ole">
						<li v-for="ty in gongye" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="xiandaiturn.length">现代</h1>
			<ul>
				<li v-for="(item,index) in xiandaiturn" @click="clickLif(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="olf">
						<li v-for="ty in xiandai" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="yznturn.length">原子能时代</h1>
			<ul>
				<li v-for="(item,index) in yznturn" @click="clickLig(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="olg">
						<li v-for="ty in yzn" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="xinxiturn.length">信息时代</h1>
			<ul>
				<li v-for="(item,index) in xinxiturn" @click="clickLih(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="olh">
						<li v-for="ty in xinxi" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
			<h1 v-show="weilaiturn.length">未来时代</h1>
			<ul>
				<li v-for="(item,index) in weilaiturn" @click="clickLil(index)">
					<span>回合</span>
					<span>{{item.Turn}}</span>
					<ol class="oll">
						<li v-for="ty in weilai" v-show="ty.Turn == item.Turn">
							<p>{{ty.CivilizationDescription}}</p>
							<h2>{{ty.InstanceDescription}}</h2>
						</li>
					</ol>
				</li>
			</ul>
		</div>
		<div class="animate" v-else>
			<img src="../assets/images/loading.gif">
		</div>
	</div>
</template>

<script>

	import axios from 'axios'

	let heighta = [];
	let heightb = [];
	let heightc = [];
	let heightd = [];
	let heighte = [];
	let heightf = [];
	let heightg = [];
	let heighth = [];
	let heightl = [];

	export default{
		data(){
			return{
				moment:{},
				yuangu:{},
				yuanguturn:{},
				gudian:{},
				gudianturn:{},
				zshiji:{},
				zshijiturn:{},
				wyfx:{},
				wyfxturn:{},
				gongye:{},
				gongyeturn:{},
				xiandai:{},
				xiandaiturn:{},
				yzn:{},
				yznturn:{},
				xinxi:{},
				xinxiturn:{},
				weilai:{},
				weilaiturn:{}
			}
		},
		methods:{
			initObj(){
				let presult = this.player.map(item =>{
					return{
						'Id':item.Id,
						'CivilizationDescription':item.CivilizationDescription,
						}
					});


				let mresult = this.moment.map(item =>{
					return{
						'Turn':item.Turn,
						'GameEra':item.GameEra,
						'ActingPlayer':item.ActingPlayer,
						'InstanceDescription':item.InstanceDescription
						}
					})

				let result = [];

			    for(var i=0;i<mresult.length;i++){
			    	for (var j = 0; j < presult.length; j++) {
			            if (mresult[i].ActingPlayer == presult[j].Id) {
			            	result.push({ 
			            		Turn: mresult[i].Turn,
			            		GameEra: mresult[i].GameEra,
			            		InstanceDescription: mresult[i].InstanceDescription,
			            		CivilizationDescription: presult[j].CivilizationDescription 
			            	});
			            }
			        }
			    }
			        
			    let initMoment = result.filter(item =>{
					return !item.InstanceDescription.match('历史上曾有两军在此交战') 
						&& !item.InstanceDescription.match('此地曾发生海战')
				})

				//初始化完毕

				this.yuangu = initMoment.filter(item =>{
					return item.GameEra == 'ERA_ANCIENT'
				})

				this.yuanguturn = JSON.parse(JSON.stringify(this.yuangu));
				
				for(var i = 0; i < this.yuanguturn.length; i++){
					for(var j = i + 1; j < this.yuanguturn.length;){
						if (this.yuanguturn[i].Turn == this.yuanguturn[j].Turn) {
							this.yuanguturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				//远古 不会封装

				this.gudian = initMoment.filter(item =>{
					return item.GameEra == 'ERA_CLASSICAL'
				})

				this.gudianturn = JSON.parse(JSON.stringify(this.gudian));

				for(var i = 0; i < this.gudianturn.length; i++){
					for(var j = i + 1; j < this.gudianturn.length;){
						if (this.gudianturn[i].Turn == this.gudianturn[j].Turn) {
							this.gudianturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.zshiji = initMoment.filter(item =>{
					return item.GameEra == 'ERA_MEDIEVAL'
				})

				this.zshijiturn = JSON.parse(JSON.stringify(this.zshiji));

				for(var i = 0; i < this.zshijiturn.length; i++){
					for(var j = i + 1; j < this.zshijiturn.length;){
						if (this.zshijiturn[i].Turn == this.zshijiturn[j].Turn) {
							this.zshijiturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.wyfx = initMoment.filter(item =>{
					return item.GameEra == 'ERA_RENAISSANCE'
				})

				this.wyfxturn = JSON.parse(JSON.stringify(this.wyfx));

				for(var i = 0; i < this.wyfxturn.length; i++){
					for(var j = i + 1; j < this.wyfxturn.length;){
						if (this.wyfxturn[i].Turn == this.wyfxturn[j].Turn) {
							this.wyfxturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.gongye = initMoment.filter(item =>{
					return item.GameEra == 'ERA_INDUSTRIAL'
				})

				this.gongyeturn = JSON.parse(JSON.stringify(this.gongye));

				for(var i = 0; i < this.gongyeturn.length; i++){
					for(var j = i + 1; j < this.gongyeturn.length;){
						if (this.gongyeturn[i].Turn == this.gongyeturn[j].Turn) {
							this.gongyeturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.xiandai = initMoment.filter(item =>{
					return item.GameEra == 'ERA_MODERN'
				})

				this.xiandaiturn = JSON.parse(JSON.stringify(this.xiandai));

				for(var i = 0; i < this.xiandaiturn.length; i++){
					for(var j = i + 1; j < this.xiandaiturn.length;){
						if (this.xiandaiturn[i].Turn == this.xiandaiturn[j].Turn) {
							this.xiandaiturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.yzn = initMoment.filter(item =>{
					return item.GameEra == 'ERA_ATOMIC'
				})

				this.yznturn = JSON.parse(JSON.stringify(this.yzn));

				for(var i = 0; i < this.yznturn.length; i++){
					for(var j = i + 1; j < this.yznturn.length;){
						if (this.yznturn[i].Turn == this.yznturn[j].Turn) {
							this.yznturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.xinxi = initMoment.filter(item =>{
					return item.GameEra == 'ERA_INFORMATION'
				})

				this.xinxiturn = JSON.parse(JSON.stringify(this.xinxi));

				for(var i = 0; i < this.xinxiturn.length; i++){
					for(var j = i + 1; j < this.xinxiturn.length;){
						if (this.xinxiturn[i].Turn == this.xinxiturn[j].Turn) {
							this.xinxiturn.splice(j, 1);
						}else{
							j++
						}
					}
				}

				this.weilai = initMoment.filter(item =>{
					return item.GameEra == 'ERA_FUTURE'
				})

				this.weilaiturn = JSON.parse(JSON.stringify(this.weilai));

				for(var i = 0; i < this.weilaiturn.length; i++){
					for(var j = i + 1; j < this.weilaiturn.length;){
						if (this.weilaiturn[i].Turn == this.weilaiturn[j].Turn) {
							this.weilaiturn.splice(j, 1);
						}else{
							j++
						}
					}
				}
			
			},
			clickLia(index){
				this.changes('.ola',index,heighta)
			},
			clickLib(index){
				this.changes('.olb',index,heightb)
			},
			clickLic(index){
				this.changes('.olc',index,heightc)
			},
			clickLid(index){
				this.changes('.old',index,heightd)
			},
			clickLie(index){
				this.changes('.ole',index,heighte)
			},
			clickLif(index){
				this.changes('.olf',index,heightf)
			},
			clickLig(index){
				this.changes('.olg',index,heightg)
			},
			clickLih(index){
				this.changes('.olh',index,heighth)
			},
			clickLil(index){
				this.changes('.oll',index,heightl)
			},
			changes(olc,index,heights){
				let  ol = document.querySelectorAll(olc);

				if (heights == '') {
					for(var i = 0;i<ol.length; i++){
						ol[i].style.height = 'auto';
						heights.push(getComputedStyle(ol[i]).height);
						ol[i].style.height = '0px';
					}
				}

				if (ol[index].style.height == '0px') {
					ol[index].style.height = heights[index]	 
				}else{
					ol[index].style.height = 0 
				}
			},
			
		},
		mounted(){
			axios
				.get('data.json')
				.then(res =>{
					this.moment = res.data.Moments;
					this.player = res.data.Players;
					this.$nextTick(() => {
						this.initObj();
					})
				})
		},
		
	}
</script>

<style lang="scss" scoped>
	.civilization{
		h1{
			font-size: .4rem;
			text-align: center;
			height: .8rem;
			line-height: .8rem;
			border-top:.02rem solid rgba(0,0,0,.1);
			&:first-child{
				border:none;
			}
		}
		ul{
			& > li{
				width: 100%;
				min-height: .7rem;
				height: auto;
				line-height: .7rem;
				font-size: .3rem;
				background: #FF708C;
				color:white;
				overflow: hidden;
				margin-bottom: .04rem;
				&:last-child{
					margin: 0;
				}
				span{
					font-size: .3rem;
					margin-left: 4%;
				}
				& > ol{
					width: 100%;
					background:white;
					height: 0;
					transition:all .5s ease-in;
					& > li{
						padding: 4%;
						font-size: .3rem;
						color:black;
						p{
							font-weight: 700;
						}
						h2{
							line-height: .5rem;
						}
					}
					
				}
			}
		}
		.animate{
			width: 100%;
			height: calc(100vh - .4rem - 1.8rem);
			display: flex;
			justify-content: center;
			align-items:center;
			img{
				width: 1rem;
				height: 1rem;
			}
		}
	}
</style>